import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";



@Injectable({
  providedIn: "root",
})
export class SupplierProfileService {

  constructor(private http: HttpClient) { }

  getSupplierProfile(): Observable<any> {
    const SupplierProfilesUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/findAll`;
    return this.http.get<any[]>(SupplierProfilesUrl);
  }
  getSupplierProfileByStatus(params: any): Observable<any> {
    const SupplierProfilesUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/find/by/status`;
    return this.http.get<any>(SupplierProfilesUrl, {params});
  }
  // http://52.15.152.26:9099/erp/profile/company-information/findAll

  // getSupplierProfileByCategory(params: any): Observable<any> {
  //   const SupplierProfilesUrl = `${environment.baseUrlSupplierManagement}/api/v1/SupplierProfile/category`;
  //   return this.http.get<any>(SupplierProfilesUrl, {params});
  // }
  

  // getSupplierProfileById(params: any): Observable<any> {
  //   const SupplierProfilesUrl= `${environment.baseUrlSupplierManagement}/api/v1/SupplierProfile/find/by/id`;

  //   return this.http.get<any>(SupplierProfilesUrl, {params});
  // }

  
  
  getSupplierProfileBySupplierCode(params: any): Observable<any> {
    const SupplierProfilesUrl= `${environment.baseUrlSupplierManagement}/erp/profile/company-information/find/by/suppliercode`;

    return this.http.get<any>(SupplierProfilesUrl, {params});
  }


  addSupplierProfile(data: any): Observable<any> {
    const SupplierProfilesUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/create`;
    return this.http.post<any>(SupplierProfilesUrl, data);
  }

  updateSupplierProfile(SupplierProfileDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/update`;
    return this.http.put<any>(updateBillUrl, SupplierProfileDetails)
  }

  deleteSupplierProfileTemporarily(params: any): Observable<any> {
    const SupplierProfilesUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/delete`;
    return this.http.put<any>(SupplierProfilesUrl, {params});
  }

  validateSupplierProfile(dataToValidate: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlSupplierManagement}/erp/profile/company-information/update/state`;
    return this.http.put<any>(updateBillUrl, dataToValidate)
  }



    // ********************************************************************************************************************



}
