import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { SupplierProfileService } from "src/app/erp-suppliersmanagement/data/supplier-profile.service";

import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-all-profiles",
  templateUrl: "./all-profiles.component.html",
  styleUrls: ["./all-profiles.component.scss"],
})
export class AllProfilesComponent implements OnInit {
  // displayedColumns: string[] = ['select', 'id', 'supplierCode', 'name', 'email', 'phone', 'address', 'category', 'prequalified', 'status', 'action'];

  displayedColumns: string[] = [
    "select",
    "id",
    "supplierCode",
    'hasKingdomAccount',
    'bankName',
    'bankAc',
    'toBankID',
    'branchName',
    'toBranchID',
    'currency',
    "status",
    "action",
  ];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };
  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "Update";
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  Form!: FormGroup;



  constructor(
    private fb: FormBuilder,
    private router: Router,
    private snackbar: SnackbarService,
    private supplierProfileService: SupplierProfileService
  ) { }

  ngOnInit(): void {
    this.Form = this.fb.group({
      status: ["PENDING", Validators.required],
    });

    this.getData();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  reqStatuses: any[] = [
    { name: "PENDING" },
    //{ name: "VERIFIED" },
    { name: "APPROVED" },
    { name: "RETURNED" },
    { name: "REJECTED" },
  ];

  selectedStatus: string = "PENDING";
  getSelectSelectedStatus() {
    this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

    this.getData();
  }

  getData() {
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.isLoading = true;
    this.selectedStatus = this.Form.value.status;

    const params = new HttpParams().set("status", this.Form.value.status);
    this.supplierProfileService
      .getSupplierProfileByStatus(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode === 302) {
            this.data = res;
            console.log("this.data: ", this.data)
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data.entity);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => { },
      }),
      Subscription;
  }

  // addSupplier() {
  //   this.router.navigate(["/erp-suppliers-management/supplier-profiles/manage-profile"]);
  // }

  refresh() {
    this.getData();
  }

  editSupplier(data: any) {
    let route = "/erp-suppliers-management/supplier-profiles/manage-profile";
    this.router.navigate([route], {
      queryParams: { id: data.supplierCode, action: "Update" },
    });
  }
  viewSupplier(data: any) {
    let route = "/erp-suppliers-management/supplier-profiles/manage-profile";
    this.router.navigate([route], {
      queryParams: { id: data.supplierCode, action: "View" },
    });
  }
  validateSupplier(data: any) {
    let route = "/erp-suppliers-management/supplier-profiles/manage-profile";
    this.router.navigate([route], {
      queryParams: { id: data.supplierCode, action: "Validate" },
    });
  }

  deleteSupplier(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Profile?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Supplier!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.supplierProfileService
          .deleteSupplierProfileTemporarily(id)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }

  //******************************************************************************************************
  //Select Suppliers
  supplierDetails: any;
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  // ISO standards for item codes //

  processMultipleRows(status: any) {
    const processedRows = this.selectedRows.map((element) => {
      return {
        id: element.id,
        status: status,
      };
    });

    console.log("LOG: ", processedRows);

    this.supplierProfileService
      .validateSupplierProfile(processedRows)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res: ", res);
          if (res.statusCode == 200) {
            this.snackbar.showNotification(
              "snackbar-success",
              res.message
            );

            this.selectedRows.splice(0, this.selectedRows.length); // Option 2: Clear the array in place
            this.selection = new SelectionModel<any>(true, []);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.getData();
        },
      }),
      Subscription;
  }
  //*************************************************************************************************************** */

}
