import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllProfilesComponent } from './pages/all-profiles/all-profiles.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { ManageProfileComponent } from './pages/manage-profile/manage-profile.component';

const routes: Routes = [
  {
    path: "all",
    component: AllProfilesComponent,
    canActivate: [RoutePrivilegeGuard],
    data: { clientName: 'SuppliersManagementModule', requiredPrivilege: ["All Supplier Profiles"] },
  },


  {
    path: "manage-profile",
    component: ManageProfileComponent,
    canActivate: [RoutePrivilegeGuard],
    data: { clientName: 'SuppliersManagementModule', requiredPrivilege: ["Manage Supplier Profile"] },
  },
];



@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SupplierProfileMaintenanceRoutingModule { }
