import { Component, Inject, OnInit } from '@angular/core';
import { ManageProfileComponent } from '../../pages/manage-profile/manage-profile.component';
import { DatePipe } from '@angular/common';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { TokenCookieService } from 'src/app/core/service/token-storage-cookies.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { SupplierProfileService } from 'src/app/erp-suppliersmanagement/data/supplier-profile.service';

@Component({
  selector: 'app-validate-supplier-profiles',
  templateUrl: './validate-supplier-profiles.component.html',
  styleUrls: ['./validate-supplier-profiles.component.sass']
})
export class ValidateSupplierProfilesComponent implements OnInit {

  horizontalPosition: MatSnackBarHorizontalPosition = "end";
  verticalPosition: MatSnackBarVerticalPosition = "top";



  Data: any;

  status!: string;

  resData: any;

  statusForm: FormGroup;

  rejected: boolean = false;
  approved: boolean = false;
  currentUser: any;
  postedBy: any;
  canVerify: boolean = false;

  validationIsLoading: boolean = false;
  hideValidation = false;

    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  constructor(
    public dialogRef: MatDialogRef<ManageProfileComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private tokenCookieService: TokenCookieService,
    private supplierProfileService: SupplierProfileService,
    private router: Router,
  ) {
    this.Data = this.data.data;
  }
  ngOnInit(): void {
    this.currentUser = this.tokenCookieService.getUser().username;


    if (this.data.action === 'deletedOrRejected') {
      this.hideValidation = true;
    }



    this.postedBy = this.Data.postedBy;

    this.canVerify = true;

    // if (this.postedBy === this.currentUser) {

    //   this.snackbar.showNotification("snackbar-danger", "You cannot verify this record!");

    //   this.canVerify = false;

    // } else {
    //   this.canVerify = true;
    // }

    console.log("this.billDet: ", this.data);

    console.log("Data: ", this.Data);

    this.statusForm = this.createStatusForm();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }


  createStatusForm(): FormGroup {
    return this.fb.group({
      reason: ["", [Validators.required]],
    });
  }

  reject() {
    this.rejected = true;
    this.approved = false;

    this.status = "REJECTED";
    if (!this.statusForm.value == null) {
      this.changeStatus();
    }
  }
  return() {
    this.rejected = true;
    this.approved = false;

    this.status = "RETURNED";
    if (!this.statusForm.value == null) {
      this.changeStatus();
    }

  }
  approve() {
    this.approved = true;
    this.rejected = false;

    this.status = "VERIFIED";
    this.statusForm.patchValue({
      reason: "NA"
    })

    this.changeStatus();
  }
 



  changeStatus() {
    this.validationIsLoading = true;
  


    const params = {
      id: this.Data.id,
      role: this.data.userType,
      status: this.status,
      remarks: this.statusForm.value.reason,
      approvedBy: this.currentUser,

    };

  
    console.log("Form = ", params);
  
    this.supplierProfileService
      .validateSupplierProfile(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res) {
            this.snackbar.showNotification(
              "snackbar-success",
              "SupplierProfile validated successfully"
            );
            
          }

        },
        error: (err) => {
          console.log("err = ", err);
          this.snackbar.showNotification(
            "snackbar-danger",
            "Server Error: " + err.message
          );
        },
        complete: () => {
          this.dialogRef.close();
          this.router.navigate(["/erp-procurement/SupplierProfiles/all-SupplierProfiles"]);
        }
      });
  }

  

}
