import { HttpParams } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { Router, ActivatedRoute } from '@angular/router';
import { BehaviorSubject, Subject, Subscription, takeUntil } from 'rxjs';
import { AccessControlService } from 'src/app/erp-procurement/data/services/_AccessControlService.service';
import { ValidateRfqComponent } from 'src/app/erp-procurement/modules/rfqs/pages/dialogs/validate-rfq/validate-rfq.component';
import { SuppliersService } from 'src/app/erp-suppliersmanagement/data/supplier-maintenance.service';
import { SupplierProfileService } from 'src/app/erp-suppliersmanagement/data/supplier-profile.service';
import { FilesService, SelectedFiles } from 'src/app/shared/services/files.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { ValidateSupplierProfilesComponent } from '../../dialogs/validate-supplier-profiles/validate-supplier-profiles.component';


interface BankStatement {
  id: number;
  file: string;
  filetype: string;
  filename: string;
}

@Component({
  selector: 'app-manage-profile',
  templateUrl: './manage-profile.component.html',
  styleUrls: ['./manage-profile.component.sass']
})
export class ManageProfileComponent implements OnInit {


  showForm = false;
  pageFunction = "Update";
  mngForm: FormGroup;
  currentUser: any;

  //formData: any;

    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  hideSubmit = false;
  hideApprovals: boolean = true;
  approvalVisible: boolean = false;
  disableActions = false;

  supplierCode: any;
  isLoading: boolean = false;

  constructor(private snackbar: SnackbarService,
    private router: Router,
    private route: ActivatedRoute,
    private supplierProfileService: SupplierProfileService,
    private fb: FormBuilder,
    private accessControlService: AccessControlService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private suppliersService: SuppliersService,) { }

  ngOnInit(): void {

    //this.getPage();
    //this.showForm = true;


    this.route.queryParams.subscribe((params) => {
      if (params.id) {
        const action = params.action;


        if (action === "Update") {
          this.pageFunction = "Update";
        } else if (action === "View") {
          this.pageFunction = "View";
          this.disableActions = true;
        } else if (action === "Validate") {
          this.pageFunction = "Validate";
          this.disableActions = true;
          this.approvalVisible = true;
        }

        this.supplierCode = params.id;
        //this.formData = JSON.parse(params.additionalData);



        console.log("ID:", this.supplierCode);

        this.getDataById();
        this.getSupplierDetailsByCode()
      }
    });

  }

  getPage(): void {

    if (this.pageFunction === "Update") {
      this.generateFormWithData();
      this.hideApprovals = true;
    } else if (this.pageFunction === "View") {
      this.generateDisabledFormWithData();
      this.hideSubmit = true;
      this.hideApprovals = true;
    } else if (this.pageFunction === "Validate") {
      this.generateFormWithData();
      this.hideApprovals = false;
      this.hideSubmit = true;
    }
  }


  generateEmptyForm(): void {
    this.mngForm = this.fb.group({
      yearEstablished: [0, Validators.required],
      companySize: ['', Validators.required],
      numberOfEmployees: [0, Validators.required],
      industry: ['', Validators.required],
      productsAndServices: ['', Validators.required],
      taxId: ['', Validators.required],
      poDeliveryEmail: ['', Validators.required],
      ownershipType: ['', Validators.required],
      bankName: ['', Validators.required],
      bankAc: ['', Validators.required]
    });
  }
  generateFormWithData(): void {
    this.mngForm = this.fb.group({
      id: [this.formData.id],
      supplierCode: [this.formData.supplierCode],
      yearEstablished: [this.formData.yearEstablished, Validators.required],
      companySize: [this.formData.companySize, Validators.required],
      numberOfEmployees: [this.formData.numberOfEmployees, Validators.required],
      industry: [this.formData.industry, Validators.required],
      productsAndServices: [this.formData.productsAndServices, Validators.required],
      taxId: [this.formData.taxId, Validators.required],
      poDeliveryEmail: [this.formData.poDeliveryEmail, Validators.required],
      ownershipType: [this.formData.ownershipType, Validators.required],
      bankName: [this.formData.bankName, Validators.required],
      bankAc: [this.formData.bankAc, Validators.required],
      mandatoryDocumentations: [this.formData.mandatoryDocumentations]
    });
  }
  generateDisabledFormWithData(): void {
    this.mngForm = this.fb.group({
      yearEstablished: [
        { value: this.formData.yearEstablished, disabled: true },
        [Validators.required],
      ],
      companySize: [
        { value: this.formData.companySize, disabled: true },
        [Validators.required],
      ],
      numberOfEmployees: [
        { value: this.formData.numberOfEmployees, disabled: true },
        [Validators.required],
      ],
      industry: [
        { value: this.formData.industry, disabled: true },
        [Validators.required],
      ],
      productsAndServices: [
        { value: this.formData.productsAndServices, disabled: true },
        [Validators.required],
      ],
      taxId: [
        { value: this.formData.taxId, disabled: true },
        [Validators.required],
      ],
      poDeliveryEmail: [
        { value: this.formData.poDeliveryEmail, disabled: true },
        [Validators.required],
      ],
      ownershipType: [
        { value: this.formData.ownershipType, disabled: true },
        [Validators.required],
      ],
      bankName: [
        { value: this.formData.bankName, disabled: true },
        [Validators.required],
      ],
      bankAc: [
        { value: this.formData.bankAc, disabled: true },
        [Validators.required],
      ],
      mandatoryDocumentations: [{ value: this.formData.mandatoryDocumentations, disabled: true },
      [Validators.required],],
    });
  }


  formData: any;
  getDataById() {
    const params = new HttpParams().set("supplierCode", this.supplierCode);
    this.supplierProfileService
      .getSupplierProfileBySupplierCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.isLoading = true;
          console.log("res :", res);
          if (res.entity) {
            this.formData = res.entity;
            this.getPage();
            this.showForm = true;

            console.log("this.formData: ", this.formData);

            if (this.formData.mandatoryDocumentations.length > 0) {
              this.isFileDataLoading = false;
              this.formData.mandatoryDocumentations.forEach((element) => {
                const newRow = this.fb.group({
                  documentType: [element.documentType],
                  file: [element.file],
                  filename: [element.filename],
                  filetype: [element.filetype],
                });
                this.rows.push(newRow);
              });

              this.updateView();
            }


            this.isLoading = false;
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => { },
      }),
      Subscription;
  }

  isLoadingSupplierDetails: boolean = true;
  supplierDetails: any;



  getSupplierDetailsByCode() {
    const params = new HttpParams().set("supplierCode", this.supplierCode);

    console.log("this.supplierCode :", this.supplierCode);

    this.suppliersService
      .getSuppliersByCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.isLoadingSupplierDetails = true;
          console.log("supplierDetails :", res);
          if (res.entity) {
            this.supplierDetails = res.entity;


            this.isLoadingSupplierDetails = false;
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
             res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => { },
      }),
      Subscription;
  }




  // **************************************************************************************************
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value.trim().toLowerCase();


  }

  //UploadDocuments

  attachementsDataSource = new BehaviorSubject<AbstractControl[]>([]);
  displayColumns = [
    "id",
    "filename",
    "filetype",
    "selectFile",
    "download",
    "action",
  ];
  rows: FormArray = this.fb.array([]);
  documentsForm: FormGroup = this.fb.group({ filedetails: this.rows });

  isFileDataLoading: boolean = true;

  fileForm: FormGroup;
  fileInputs: FormArray;

  createFileForm() {
    this.fileForm = this.fb.group({
      files: this.fb.array([]),
    });
    //this.fileInputs = this.fileForm.get('files') as FormArray;
    this.addFileRow();
  }

  addFileRow() {
    this.isFileDataLoading = false;
    const row = this.fb.group({
      documentType: ["", Validators.required],
      file: ["", Validators.required],
      filetype: ["", Validators.required],
      filename: ["", Validators.required],
    });
    //this.fileInputs.push(row);
    this.rows.push(row);
    this.attachementsDataSource.next(this.rows.controls);
  }

  updateView() {
    this.attachementsDataSource.next(this.rows.controls);
  }

  // deleteCall(index){
  //   this.dataSource.splice(index, 1);
  // }

  deleteCall(row: AbstractControl) {
    const dataArray = this.attachementsDataSource.getValue(); // Get the underlying array
    const index = dataArray.indexOf(row); // Find the index of the row

    if (index !== -1) {
      dataArray.splice(index, 1); // Remove 1 item at the specified index
      this.attachementsDataSource.next(dataArray); // Update the BehaviorSubject with the modified array
    }
  }

  public selectedFiles: SelectedFiles[] = [];
  public isFileLoading = new BehaviorSubject(false);

  currFile: string;
  currFilename: any;
  currType: any;

  onSelectFile(files, selectedRow, index) {
    console.log("row: ", selectedRow.value, index);
    this.isFileLoading.next(true);
    this.selectedFiles = [];
    this.filesService.toBase64(files, this.selectedFiles).subscribe((res) => {
      if (res) {
        this.isFileLoading.next(false);

        this.selectedFiles = res;
        console.log("selectedFiles: ", this.selectedFiles);

        console.log("this.selectedFiles[0].name: ", this.selectedFiles[0].name);

        this.currFile = this.selectedFiles[0].base64;

        const fileName = this.selectedFiles[0].name;
        const fileParts = fileName.split(".");
        const name = fileParts.slice(0, -1).join("."); // Join all parts except the last one
        const extension = fileParts[fileParts.length - 1];

        console.log("Name:", name); // Output: Lease Armotization (3)
        console.log("Extension:", extension); // Output: xlsx

        this.currFilename = name;
        this.currType = extension;

        this.rows.at(index).patchValue({
          file: this.currFile,
          filename: this.currFilename,
          filetype: this.currType,
        });
        this.updateView();
      }
    });
  }

  downloadDocument(row: any) {
    let file = row.value.file;
    let filename = row.value.filename;
    let filetype = row.value.filetype;

    // Download PDF in Chrome etc.
    const source = `${file}`;
    const link = document.createElement("a");
    link.href = source;
    link.download = `${filename}.${filetype}`;
    link.click();
  }


  //*************************************************************************************************** */




  cancel(): void {
    this.router.navigate(["/erp-suppliers-management/supplier-profiles/all"]);
  }

  // ****************************************************************************************************************************

  hasAccess: boolean;

  onValidate(userType) {
    let privilege = "Validate Supplier Profile";

    this.hasAccess = this.accessControlService.hasPrivilege('SuppliersManagementModule', [privilege]);

    if (this.hasAccess) {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.autoFocus = true;
      dialogConfig.width = "650px";
      dialogConfig.data = {
        data: this.formData,
        userType: userType,
      };

      const dialogRef = this.dialog.open(ValidateSupplierProfilesComponent, dialogConfig);

      dialogRef.afterClosed().subscribe((result) => {
        this.router.navigate(["/erp-suppliers-management/supplier-profiles/all"]);
      });
    }
  }


  

  onSubmit() {
    console.log("this.mngForm.value: ", this.mngForm.value);

    if (this.pageFunction == "Add") {
      this.supplierProfileService
        .addSupplierProfile([this.mngForm.value])
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 201) {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
              this.router.navigate([
                "/erp-suppliers-management/supplier-profiles/all",
              ]);
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
               res.message
              );
            }
          },
          error: (err) => {
            this.snackbar.showNotification(
              "snackbar-danger",
              err.message
            );
          },
          complete: () => {},
        }),
        Subscription;
    } else if (this.pageFunction == "Update") {
      this.supplierProfileService
        .updateSupplierProfile(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res :",res)
            if (res) {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
              this.router.navigate([
                "/erp-suppliers-management/supplier-profiles/all",
              ]);
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
              );
            }
          },
          error: (err) => {
            this.snackbar.showNotification(
              "snackbar-danger",
              err.message
            );
          },
          complete: () => {},
        }),
        Subscription;
    }
  }
}
